@import 'vars';
html {
    width: 100%;
}

.wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
}

.content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
}

body {
    font-family: $font1;
    font-weight: 300;
    color: $black;
    &.fixed-body {
        overflow-y: hidden;
    }
}

.container {
    max-width: 1240px;
    margin: 0 auto;
    padding: 0 35px;
}

a {
    &:hover {
        color: $primary;
    }
}


@media screen and (max-width:400px){
    .container{
        padding: 0 15px;
    }
}
