@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    font-size: 14px;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 0.02em;
    font-family: $font2;
    height: 46px;
    padding: 0 21px;
    border-radius: 5px;
    &:focus {
        box-shadow: none;
    }
}


.m-btn-primary{
    color: #ffffff;
    background-color: $primary;
    &:hover{
        background-color: darken($primary,10%);
        color: #ffffff;
    }
}


.line-btn{
    position: relative;
    &:before{
        content: '';
        width: calc(100% + 40px);
        height: 1px;
        background-color: inherit;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        display: block;
        position: absolute;
        transition: inherit;
    }
    span{
        position: relative;
        z-index: 2;
    }
}
