.accent-text{
  color: $primary;
}

.line{
  position: relative;
  &:before{
    content: '';
    width: 210px;
    height: 1px;
    background-color: $primary;
    right: 100%;
    top: 22px;
    display: block;
    position: absolute;
  }
}

.display1{
  font-size: 40px;
  line-height: 112.5%;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.02em;
  font-family: $font2;
}

.display2{
  font-size: 24px;
  line-height: 112.5%;
  font-weight: 700;
  font-family: $font2;
}

.display3{
  font-size: 15px;
  line-height: 22px;
  font-weight: 700;
}

.body-text{
  font-size: 16px;
  line-height: 160%;
  font-weight: 300;
  letter-spacing: 0.02em;
  p{
    line-height: inherit;
    &:not(:last-child){
      margin-bottom: 20px;
    }
  }
}


@media screen and (max-width:576px){
  .display1{
    font-size: 30px;
  }

  .line{
    &:before{
      top: 16px;
    }
  }

  .body-text{
    font-size: 14px;
  }
}
