.footer{
  background-color: $secondary;
  padding: 60px 0;
  color: #ffffff;
  &__logo{
    display: block;
    width: 150px;
    margin-bottom: 24px;
    img{
      width: 100%;
      display: block;
    }
  }
  &__inner{
    display: grid;
    grid-template-columns: repeat(4,auto);
    justify-content: space-between;
  }
  &__title,
  &__link{
    font-size: 14px;
    line-height: 30px;
  }
  &__title{
    font-weight: 500;
  }
  &__link{
    font-weight: 300;
    display: block;
  }
  &__logo2{
    width: 235px;
    display: block;
    img{
      width: 100%;
      display: block;
    }
  }
}


@media screen and (max-width:1200px){
  .footer{
    &__inner{
      grid-template-columns: repeat(3,auto);
      gap: 23px;
    }
    &__col{
      &:nth-child(3){
        grid-row: 1/3;
        grid-column: 3/4;
      }
      &:nth-child(4){
        grid-column: 1/3;
        grid-row: 2/3;
      }
    }
  }
}


@media screen and (max-width:768px){
  .footer{
    &__logo{
      width: 130px;
      margin-bottom: 28px;
    }
    &__logo2{
      width: 220px;
    }
    &__title,
    &__link{
      font-size: 12px;
      line-height: 14px;
      &:not(:last-child){
        margin-bottom: 10px;
      }
    }
  }
}


@media screen and (max-width:576px){
  .footer{
    padding-top: 55px;
    &__inner{
      grid-template-columns: repeat(2,auto);
    }
    &__col{
      &:nth-child(3){
        display: none;
      }
    }
    &__logo{
      width: 115px;
    }
  }
}
