.fg {
  position: relative;
  margin: 0;
  input,
  select,
  textarea{
    border-radius: 5px;
    border: $border;
    padding: 0 16px;
    height: 46px;
    width: 100%;
    font-size: 18px;
    line-height: 120%;
    background-color: transparent;
    color: $black;
    &::placeholder {
      color: rgba($black, 0.6);
    }
    &:focus{
      border-color: $primary;
    }
  }
  textarea{
    resize: none;
    padding-top: 20px;
    padding-bottom: 20px;
    height: 120px;
  }
  select{
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    -o-appearance: none;
    appearance: none;
    background-image: url(../img/select_arrow.webp);
    background-repeat: no-repeat;
    background-position: right 17px center;
    &::-ms-expand {
      display: none; // IE
    }
  }
  &__icon{
    position: absolute;
    padding-right: 14px;
    left: 22px;
    top: 50%;
    transform: translateY(-50%);
    border-right: 1px solid #dce4e9;
    display: flex;
    align-items: center;
    svg{
      width: 27px;
      height: auto;
    }
  }
}

.m-check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        background-image: url(../img/check.svg);
      }
    }
  }
  label {
    padding-left: 40px;
    position: relative;
    min-height: 24px;
    font-size: 15px;
    line-height: 20px;
    cursor: pointer;
    display: flex;
    align-items: center;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      border-radius: 4px;
      border: $border;
      background-repeat: no-repeat;
      background-position: center;
      width: 24px;
      height: 24px;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
    }
  }
  a{
    position: relative;
    z-index: 2;
    color: $primary;
    text-decoration: underline;
  }
}


.cartcalc{
  width: 98px;
  height: 46px;
  display: flex;
  align-items: center;
  border-radius: 5px;
  border: $border;
  padding: 0 15px;
  input{
    text-align: center;
    margin: 0 2px;
    flex-grow: 1;
    width: 100%;
    font-size: 15px;
    font-weight: 300;
    letter-spacing: 0.002em;
    border: none;
  }
  button{
    flex-shrink: 0;
    display: flex;
    align-items: center;
  }
}
