.catalog {
  overflow: hidden;
  &__title {
    margin-bottom: 35px;
    br {
      display: none;
    }
  }
  &__content {
    padding-bottom: 50px;
    position: relative;
    &:not(:last-child) {
      margin-bottom: 40px;
    }
   &:not(:first-child) {
     &:after {
       content: '';
       position: absolute;
       left: -5000px;
       bottom: 0;
       width: calc(100% + 10000px);
       height: 1px;
       background-color: #e8edf1;
     }
   }
  }
  &__subtitle {
    font-size: 24px;
    line-height: 120%;
    font-weight: 700;
    color: $black;
    margin-bottom: 20px;
  }
  &__desc {
    p {
      font-size: 15px;
      line-height: 160%;
      font-weight: 300;
      color: #00355b;
      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }
  }
  &__img {
    float: left;
    margin: 0 28px 17px 0;
    img {
      width: 100%;
      height: auto;
    }
  }
  &__links {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 40px;
    margin-top: 35px;
    &-left,
    &-right {
      border: $border;
    }
  }
  &__link {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px;
    font-size: 14px;
    line-height: 120%;
    font-weight: 300;
    color: $black;
    &:not(:last-child) {
      border-bottom: $border;
    }
    span {
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 1px;
        background-color: $primary;
        opacity: 0;
      }
    }
    i {
      margin-left: 10px;
    }
    &:hover {
      span:after {
        transition: all 0.5s;
        opacity: 1;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .catalog {
    &__links {
      gap: 30px;
      margin-top: 25px;
    }
  }
}

@media screen and (max-width: 992px) {
  .catalog {
    &__title {
      br {
        display: block;
      }
    }
    &__links {
      display: block;
      max-width: 440px;
      border-top: $border;
      border-right: $border;
      border-left: $border;
      &-left,
      &-right {
        border: none;
      }
    }
    &__link {
      border-bottom: $border;
    }
  }
}

@media screen and (max-width: 768px) {
  .catalog {
    &__img {
      margin: 0 15px 15px 0;
      max-width: 213px;
    }
  }
}

@media screen and (max-width: 576px) {
  .catalog {
    &__img {
      float: unset;
      margin: 0 0 15px;
    }
    &__subtitle {
      font-size: 18px;
      margin-bottom: 15px;
    }
    &__links {
      margin-top: 15px;
    }
  }
}
