.sidebar{
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  background-color: #1583cc;
  width: 86px;
  display: flex;
  flex-direction: column;
  padding: 30px 0;
  &__nav{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__btn{
    &:not(:last-child){
      margin-bottom: 40px;
    }
  }
  &__btn,
  &__social{
    img{
      transition: all .3s;
    }
    &:hover{
      img{
        transform: translateY(-10px);
      }
    }
  }
  &__bottom{
    margin-top: auto;
    padding-top: 40px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__social{
    &:not(:last-child){
      margin-bottom: 50px;
    }
  }
  &__consult{
    width: 46px;
    height: 243px;
    background-color: #00355b;
    border-radius: 5px;
    margin-top: 100px;
    position: relative;
    &:hover{
      background-color: darken(#00355b,10%);
    }
    span{
      line-height: 30px;
      font-weight: 700;
      font-family: $font2;
      text-transform: uppercase;
      letter-spacing: 0.02em;
      color: #ffffff;
    }

    @media screen and (min-width:1401px){
      span{
        top: 50%;
        left: 50%;
        position: absolute;
        writing-mode: tb-rl;
        transform: rotate(-180deg) translate(50%,50%);
        white-space: nowrap;
      }
    }
  }
}

.header{
  position: relative;
  z-index: 4;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  &__logo{
    display: block;
    width: 247px;
    img{
      width: 100%;
      display: block;
    }
  }
  &__item{
    display: flex;
  }
  &__icon{
    flex-shrink: 0;
    margin-right: 7px;
  }
  &__info{
    font-size: 12px;
    font-family: $font2;
    display: grid;
    grid-template-columns: 100%;
    gap: 8px;
    font-weight: 400;
    text-transform: uppercase;
    letter-spacing: 0.02em;
  }
  &__phone{
    font-size: 20px;
    b{
      font-weight: 900;
    }
  }
  &__mail{
    color: $primary;
  }
  &--light{
    color: #fff;
    .nav{
      padding-bottom: 0;
      border: none;
    }
  }
}

.burger{
  display: none;
}

.navbar{
  padding-top: 51px;
}

.nav{
  width: 100%;
  margin-top: 45px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 7px;
  border-bottom: 3px solid $primary;
  &__item{
    position: relative;
    font-size: 17px;
    line-height: 30px;
    font-weight: 500;
    letter-spacing: 0.002em;
    display: flex;
    align-items: center;
    white-space: nowrap;
    &:not(:last-child){
      flex-grow: 1;
      justify-content: space-between;
      &:after{
        content: '';
        width: 50px;
        height: 1px;
        flex-shrink: 1;
        background-color: $primary;
        display: block;
        margin: 0 auto;
      }
    }
    &--active{
      .nav__hidden{
        opacity: 1;
        pointer-events: unset;
        transform: translateY(0);
      }
    }
  }
  &__hidden{
    position: absolute;
    top: 0;
    left: 0;
    min-width: 100%;
    padding-top: 30px;
    &-inner{
      border: 1px solid $primary;
      background-color: #fff;
    }
    @media screen and (min-width:901px){
      transition: all .3s;
      opacity: 0;
      transform: translateY(50%);
    }
  }
  &__show{
    &:hover{
      cursor: pointer;
      color: $primary;
    }
  }
  &__sublink{
    display: flex;
    align-items: center;
    height: 45px;
    padding: 0 30px;
    &:not(last-child){
      border-bottom: 1px solid $primary;
    }
    &:hover{
      background-color: $primary;
      color: #fff;
    }
  }
}


@media screen and (max-height:800px){
  .sidebar{
    &__social,
    &__btn{
      &:not(:last-child){
        margin-bottom: 25px;
      }
    }
    &__consult{
      margin-top: 50px;
    }
  }
}


@media screen and (max-width:1400px){
  .nav{
    &__item{
      &:after{
        width: 40px !important;
      }
    }
  }
}


@media screen and (max-width:1320px){
  .nav{
    &__item{
      &:after{
        width: 30px !important;
      }
    }
  }
}


@media screen and (max-width:1400px){
  .header{
    &__btn{
      display: none;
    }
  }

  .sidebar{
    position: static;
    align-items: center;
    flex-direction: row;
    width: 100%;
    height: 59px;
    padding: 0 35px;
    &__nav,
    &__socials,
    &__bottom{
      align-items: center;
      flex-direction: row;
    }
    &__btn,
    &__social{
      &:not(:last-child){
        margin: 0 40px 0 0;
      }
    }
    &__bottom{
      margin: 0 0 0 auto;
      padding: 0;
    }
    &__consult{
      width: 233px;
      height: 40px;
      margin: 0 0 0 40px;
    }
  }

  .nav{
    margin-top: 23px;
    &__item{
      &:after{
        width: 30px !important;
      }
    }
  }

  .navbar{
    padding-top: 30px;
  }
}


@media screen and (max-width:1100px){
  .header{

  }
}


@media screen and (max-width:992px){
  .nav{
    &__item{
      &:after{
        width: 16px !important;
      }
    }
  }
}


@media screen and (max-width:900px){
  .nav{
    position: fixed;
    left: 0;
    top: 200px;
    width: 100%;
    height: calc(100vh - 200px);
    flex-direction: column;
    justify-content: unset;
    padding: 20px 35px;
    overflow-y: auto;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    margin: 0;
    background-color: $secondary;
    z-index: 10;
    &__item{
      display: block;
      font-size: 20px;
      flex-grow: unset !important;
      margin: 0 auto 25px 0;
      &:after{
        display: none !important;
      }
    }
    &__hidden{
      position: static;
      padding-top: 15px;
      display: none;
      &-inner{
        border: none;
        background-color: transparent;
      }
    }
    &__sublink{
      border: none !important;
      padding: 0 0 0 30px;
      font-size: 18px;
    }
  }

  .navbar{
    padding: 21px 0;
    transition: all .3s;
  }

  .burger{
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    span{
      display: block;
      width: 100%;
      height: 4px;
      border-radius: 4px;
      background: red;
      left: 0;
      transition: all .3s;
      &:nth-child(1){
        position: absolute;
        top: 0;
      }
      &:nth-child(3){
        position: absolute;
        bottom: 0;
      }
    }
  }


  .header{
    &__inner{
      display: block;
      position: relative;
      padding: 0 40px 0 50%;
    }
    &__logo{
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
    }
    &__item{
      &:not(:last-child){
        margin-bottom: 17px;
      }
    }

    &._open{
      .burger{
        span{
          &:nth-child(1){
            transform: rotate(-45deg) translate(-13px,5px);
          }
          &:nth-child(2){
            transform: scale(0);
          }
          &:nth-child(3){
            transform: rotate(45deg) translate(-13px,-5px);
          }
        }
      }
      .nav{
        opacity: 1;
        pointer-events: unset;
      }
      .navbar{
        background-color: $secondary;
      }
    }
  }

  .sidebar{
    &__consult{
      font-size: 12px;
      width: 204px;
      margin-left: 20px;
    }
    &__btn,
    &__social{
      &:not(:last-child){
        margin-right: 20px;
      }
      img{
        max-width: 31px;
      }
    }
  }
}


@media screen and (max-width:620px){
  .header{
    &__inner{
      padding: 0;
      display: flex;
    }
    &__item{
      display: none;
    }
    &__logo{
      position: static;
      transform: unset;
      width: 185px;
    }
  }

  .burger{
    position: relative;
    transform: unset;
  }

  .navbar{
    padding: 30px 0;
  }

  .nav{
    top: 165px;
    height: calc(100vh - 165px);
  }
}


@media screen and (max-width:550px){
  .sidebar{
    &__nav{
      display: none;
    }
    &__bottom{
      flex-grow: 1;
      justify-content: space-between;
    }
    &__social{
      img{
        max-width: 25px;
      }
      &:not(:last-child){
        margin-right: 15px;
      }
    }
  }
}


@media screen and (max-width:400px){
  .sidebar{
    padding: 0 15px;
  }
}
