.catalog-item{
  padding-bottom: 50px;
  &__title{
    text-transform: unset;
  }
  &__inner{
    display: grid;
    grid-template-columns: 36% auto;
    margin-top: 40px;
    gap: 6%;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__props{
    border: 3px solid #1583cc;
    padding: 0 25px;
  }
  &__prop{
    padding: 20px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 15px;
    line-height: 22px;
    &:not(:last-child){
      border-bottom: $border;
    }
    &-desc{
      font-weight: 700;
      color: #1583cc;
      white-space: nowrap;
      margin-left: 5px;
    }
  }
}

.features{
  margin-top: 42px;
  &__title{
    margin-bottom: 24px;
  }
  &__group{
    max-width: 1090px;
    &:not(:last-child){
      margin-bottom: 20px;
    }
  }
  &__subtitle{
    margin-bottom: 10px;
  }
  &__desc{
    font-size: 15px;
    line-height: 22px;
    font-weight: 300;
    ul,
    p{
      line-height: inherit;
    }
    li{
      display: flex;
      line-height: inherit;
      &:before{
        content: '';
        width: 6px;
        height: 6px;
        border-radius: 50%;
        flex-shrink: 0;
        margin-right: 14px;
        background-color: $primary;
        transform: translateY(7px);
      }
      &:not(:last-child){
        margin-bottom: 7px;
      }
    }
  }
}

.props{
  margin-top: 40px;
  &__grid{
    margin-top: 25px;
    column-count: 2;
    column-gap: 40px;
  }
  &__item{
    width: 100%;
    display: inline-flex;
    align-items: center;
    justify-content: space-between;
    min-height: 41px;
    border: $border;
    padding: 3px 16px;
    font-size: 15px;
    line-height: 120%;
    font-weight: 300;
    &:not(:first-child){
      margin-top: -1px;
    }
    &-desc{
      white-space: nowrap;
      font-weight: 700;
    }
  }
}

.catalog-advantages{
  margin-top: 45px;
  &__desc{
    margin-top: 20px;
    font-size: 15px;
    line-height: 22px;
    font-weight: 300;
  }
}

.product{
  margin-top: 40px;
  &__slider{
    margin-top: 25px;
  }
  .swiper{
    &-container{
      overflow: hidden;
      padding: 0 1px;
      width: calc(100% + 2px);
      margin-left: -1px;
    }
    &-wrapper{
      align-items: stretch;
    }
    &-pagination{
      display: none;
      position: static;
      margin-top: 19px;
    }
  }
  .product-card{
    height: auto;
    &:not(:last-child){
      border-right: none;
    }
  }
}


@media screen and (max-width:1024px){
  .catalog-item{
    &__inner{
      grid-template-columns: 37.5% auto;
      gap: 1.5%;
    }
  }

  .features{
    margin-top: 32px;
  }

  .props{
    &__grid{
      column-count: 1;
    }
  }
}


@media screen and (max-width:800px){
  .catalog-item{
    &__inner{
      grid-template-columns: 100%;
      gap: 32px;
      margin-top: 28px;
    }
    &__img{
      max-width: 345px;
      margin: 0 auto;
    }
  }
}


@media screen and (max-width:768px){

}


@media screen and (max-width:576px){
  .catalog-item{
    &__props{
      padding: 0 20px;
    }
    &__prop{
      font-size: 14px;
      line-height: 20px;
      padding: 14px 0;
      min-height: 60px;
    }
  }

  .props{
    &__item{
      font-size: 14px;
    }
  }

  .catalog-advantages{
    margin-top: 25px;
    &__desc{
      margin-top: 20px;
    }
  }

  .product{
    .swiper{
      &-pagination{
        display: flex;
      }
    }
  }
}
