.main{
  margin-top: -190px;
  color: #ffffff;
  overflow: hidden;
  position: relative;
  &__slide{
    min-height: 850px;
    height: auto;
    padding-top: 180px;
    background-size: cover;
    background-position: center;
    overflow: hidden;
  }
  &__content{
    padding: 170px 0;
  }
  &__title{
    max-width: 410px;
  }
  &__desc{
    margin-top: 25px;
    font-size: 19px;
    line-height: 147%;
    letter-spacing: 0.02em;
    max-width: 740px;
  }
  &__btn{
    margin-top: 31px;
    width: 160px;
  }
  &__nav{
    position: absolute;
    bottom: 170px;
    width: 100%;
    z-index: 2;
  }
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-pagination{
      position: static;
      text-align: left;
      padding-left: 190px;
      min-height: 46px;
      display: flex;
      align-items: center;
    }
  }
}

.equip{
  color: #ffffff;
  background-color: $secondary;
  padding: 60px 0;
  &__title{
    max-width: 315px;
  }
  &__grid{
    @media screen and (min-width:576px){
      margin-top: 50px;
      display: grid;
      grid-template-columns: repeat(3,calc(100% / 3));
      transform: unset !important;
    }
  }
  .swiper-slide{
    width: 100% !important;
  }
  .swiper-pagination{
    display: none;
  }
}

.about{
  padding: 50px 0;
  color: $dark;
  &__title{
    max-width: 230px;
  }
  &__text{
    margin-top: 50px;
    column-count: 2;
    column-gap: 130px;
  }
}

.certificates{
  margin-top: 46px;
  .swiper{
    &-container{
      overflow: hidden;
    }
    &-pagination{
      margin-top: 45px;
    }
  }
}

.advantages{
  margin-top: 60px;
  @media screen and (min-width:577px){
    .swiper{
      &-wrapper{
        display: grid;
        transform: unset !important;
      }
      &-slide{
        width: 100% !important;
      }
      &-pagination{
        display: none;
      }
    }
  }

  @media screen and (min-width:993px){
    .swiper{
      &-wrapper{
        grid-template-columns: repeat(4,25%);
      }
    }
    .advantages-card{
      &:not(:last-child){
        border-right: none;
      }
    }
  }
}

.partners{
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: 130px;
  &__item{
    height: 92px;
    display: flex;
    align-items: center;
    max-width: 195px;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
}

.news{
  padding: 50px 0;
  border-top: $border;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__link{
    width: 160px;
  }
  &__slider{
    margin-top: 50px;
    overflow: hidden;
  }
}

.contacts{
  margin-top: 50px;
  overflow: hidden;
  &__inner{
    display: grid;
    grid-template-columns: 400px auto;
    gap: 20px;
  }
  &__title{
    max-width: 220px;
  }
  &__select{
    margin-top: 55px;
    max-width: 240px;
  }
  &__map{
    height: 521px;
    width: 1120px;
    iframe{
      width: 100%;
      height: 100%;
      display: block;
    }
  }
  &__items{
    margin-top: 35px;
    display: grid;
    grid-template-columns: 100%;
    gap: 22px;
  }
  &__item{
    display: flex;
  }
  &__icon{
    width: 16px;
    display: block;
    flex-shrink: 0;
    margin-right: 10px;
    img{
      max-width: 100%;
    }
  }
  &__subtitle{
    font-weight: 500;
    margin-bottom: 4px;
  }
  &__subtitle,
  &__desc{
    font-size: 16px;
    line-height: 22px;
    letter-spacing: 0.02em;
  }
  &__desc{
    font-weight: 300;
    a{
      display: block;
      line-height: inherit;
    }
  }
}


@media screen and (max-width:1200px){
  .about{
    &__text{
      column-gap: 30px;
    }
  }

  .partners{
    margin: 30px 0 0 -20px;
    width: calc(100% + 40px);
    &__item{
      max-width: 152px;
      height: 71px;
      margin: 30px 20px 0;
    }
  }

  .contacts{
    &__map{
      width: calc(100% + 35px);
    }
  }
}


@media screen and (max-width:992px){
  .main{
    margin-top: -170px;
    &__slide{
      padding-top: 130px;
      min-height: unset;
    }
    &__content{
      padding: 103px 0 113px;
    }
    &__nav{
      bottom: 113px;
    }
    .swiper{
      &-pagination{

      }
    }
  }

  .about{
    &__text{
      column-count: 1;
    }
  }

  .advantages{
    margin-top: 50px;
    @media screen and (min-width:577px){
      .swiper{
        &-wrapper{
          grid-template-columns: repeat(2,50%);
        }
      }
      .advantages-card{
        &:nth-child(odd){
          border-right: none;
        }
      }
    }
  }

  .partners{
    &__item{
      max-width: 114px;
      height: 54px;
    }
  }

  .contacts{
    &__inner{
      grid-template-columns: 330px auto;
      gap: 25px 15px;
    }
  }
}


@media screen and (max-width:768px){
  .contacts{
    margin-top: 10px;
    &__inner{
      grid-template-columns: 100%;
    }
    &__select{
      margin-top: 47px;
    }
    &__items{
      @media screen and (min-width:576px){
        grid-template-columns: 1fr 1fr;
        gap: 28px;
      }
    }
    &__item{
      @media screen and (min-width:576px){
        &:nth-child(4){
          grid-column: 1/2;
          grid-row: 2/3;
        }
      }
    }
    &__map{
      height: 350px;
      width: calc(100% + 70px);
      margin-left: -35px;
    }
  }
}


@media screen and (max-width:620px){
  .main{
    margin-top: -110px;
    &__slide{
      padding-top: 85px;
    }
  }
}

@media screen and (max-width:576px){
  .main{
    &__desc{
      font-size: 14px;
      line-height: 22px;
    }
  }

  .equip{
    .swiper{
      &-container{
        margin-top: 35px;
        overflow: hidden;
      }
      &-pagination{
        position: static;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 40px;
      }
    }
  }

  .about{
    &__text{
      margin-top: 30px;
    }
  }

  .advantages{
    .swiper{
      &-container{
        overflow: hidden;
      }
      &-pagination{
        margin-top: 40px;
        display: flex;
      }
    }
  }

  .news{
    &__link{
      width: 140px;
    }
  }

  .contacts{
    &__inner{
      gap: 35px;
    }
    &__select{
      margin-top: 37px;
    }
    &__items{
      margin-top: 24px;
    }
    &__map{
      height: 450px;
    }
  }
}

