@import "../vars.scss";
.breadcrumbs {
    margin: 30px 0 5px;
    &__item {
        font-size: 12px;
        text-transform: uppercase;
        letter-spacing: 0.02em;
        color: #00355b;
        position: relative;
        font-weight: 400;
        display: inline;
        font-family: $font2;
        &:not(:first-child) {
            &:before {
                content: '';
                //position: absolute;
                z-index: 1;
                width: 4px;
                height: 4px;
                border-radius: 50%;
                background-color: #1583cc;
                margin: 0 8px 0 4px;
                display: inline-block;
                transform: translateY(-3px);
            }
        }
    }
}
