.equip-card{
  position: relative;
  display: block;
  &__img{
    position: relative;
    padding-bottom: 100%;
    border: 2px solid $primary;
    img{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__title{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    font-size: 20px;
    line-height: 125%;
    font-weight: 700;
    text-align: center;
    width: 100%;
    padding: 0 5px;
  }
}

.certificate-card{
  display: block;
  &__img{
    display: block;
    position: relative;
    padding-bottom: 143%;
    img{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}

.advantages-card{
  position: relative;
  padding-bottom: 100%;
  border: $border;
  &__content{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    justify-content: center;
  }
  &__icon{
    height: 77px;
    display: flex;
    align-items: center;
    justify-content: center;
    img{
      margin: 0 auto;
      max-height: 100%;
      display: block;
    }
  }
  &__title{
    margin-top: 20px;
    text-align: center;
    font-size: 18px;
    line-height: 140%;
    font-weight: 700;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  &__desc{
    margin-top: 5px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:2;
    -webkit-box-orient: vertical;
  }
}

.news-card{
  display: flex;
  align-items: center;
  &__img{
    width: 100px;
    height: 100px;
    flex-shrink: 0;
    margin-right: 15px;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__date{
    font-size: 16px;
    line-height: 125%;
    font-weight: 200;
    color: #041622;
  }
  &__title{
    font-size: 15px;
    line-height: 133%;
    font-weight: 700;
    margin-top: 8px;
  }
}

.product-card{
  border: $border;
  padding: 30px 12px 27px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  &__img{
    height: 180px;
    display: flex;
    align-items: center;
    justify-content: center;
    img{
      max-height: 100%;
      max-width: 100%;
    }
  }
  &__title{
    margin-top: 21px;
    font-size: 16px;
    line-height: 18px;
    font-weight: 700;
  }
  &__status{
    margin: 5px 0 19px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 13px;
    line-height: 120%;
    img{
      margin-right: 6px;
      flex-shrink: 0;
    }
  }
  &__btn{
    margin-top: auto;
  }
}


@media screen and (max-width:992px){
  .equip-card{
    &__title{
      font-size: 18px;
    }
  }
}


@media screen and (max-width:768px){
  .equip-card{
    &__title{
      font-size: 13px;
    }
  }
}


@media screen and (max-width:768px){
  .equip-card{
    &__title{
      font-size: 20px;
    }
  }
}
