@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
.swiper-pagination{
  z-index: 1;
}

.num-pag{
  display: flex;
  align-items: center;
  .swiper-pagination{
    &-bullet{
      width: unset;
      height: unset;
      opacity: 1;
      background-color: transparent !important;
      font-size: 20px;
      font-weight: 900;
      font-family: $font2;
      display: flex;
      align-items: center;
      margin: 0 !important;
      &:not(:last-child){
        &:after{
          flex-shrink: 0;
          content: '';
          width: 13px;
          height: 1px;
          display: block;
          background-color: $primary;
          margin: 0 10px;
        }
      }
      &-active{
        color: $primary;
      }
    }
  }
}


.round-pag{
  .swiper-pagination{
    &-bullet{
      width: 12px;
      height: 12px;
      background-color: #00355b;
      opacity: 1;
      margin: 0 9px !important;
      &-active{
        background-color: $primary;
      }
    }
  }
}

.line-pag{
  position: relative;
  justify-content: center;
  &:before,
  &:after{
    content: '';
    display: block;
    width: 40px;
    height: 1px;
    background-color: $primary;
    margin: 0 10px;
    position: relative;
    justify-content: center;
  }
  .swiper-pagination{
    &-bullet{
      color: $primary;
      &-active{
        color: $black;
      }
    }
  }
}
