.modal {
    background: rgba(#000, .87);
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    overflow-y: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    opacity: 0;
    pointer-events: none;
    &--active{
        opacity: 1;
        pointer-events: unset;
    }
    &__dialog{
        border-radius: 8px;
        background-color: #fff;
        width: 720px;
        max-height: 100vh;
        overflow-y: auto;
        padding: 32px;
        position: relative;
    }
    &__title,
    &__desc{
        text-align: center;
    }
    &__desc{
        margin-top: 20px;
    }
    &__close{
        position: absolute;
        top: 20px;
        right: 20px;
        svg{
            width: 30px;
            height: auto;
        }
    }
    &__form{
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 16px;
        margin-top: 17px;
    }
    &__hint{
        font-size: 16px;
    }
    &__btn{
        height: 64px;
        width: 100%;
        @media screen and (min-width: 768px){
            grid-column: 2/3;
            grid-row: 3/4;
        }
    }
    .fg{
        input{
            height: 64px;
        }
        &--text{
            @media screen and (min-width: 768px){
                grid-column: 2/3;
                grid-row: 1/3;
                textarea{
                    height: 100%;
                }
            }
        }
    }
    &__check{
        grid-column: 1/-1;
    }
}


@media screen and (max-width: 992px){
    .modal{
        &__dialog{
            padding: 40px 16px 70px;
        }
    }
}


@media screen and (max-width: 768px){
    .modal{
        &__form{
            grid-template-columns: 100%;
        }
    }
}
