.zap{
  padding-bottom: 45px;
  &__title{
    text-transform: unset;
  }
  &__desc{
    margin-top: 40px;
    font-size: 15px;
    line-height: 22px;
    font-weight: 300;
  }
  &__btn{
    width: 200px;
    margin: 30px auto 0;
  }
}

.zap-table{
  margin-top: 25px;
  &__head{
    padding: 17px 0;
    border-bottom: 3px solid $primary;
    font-size: 17px;
    font-weight: 500;
    letter-spacing: 0.002em;
  }
  &__row{
    display: grid;
    grid-template-columns: auto 38.9%;
    gap: 5px;

  }
  &__title{
    font-size: 15px;
    line-height: 22px;
    font-weight: 300;
  }
  &__item{
    align-items: center;
    padding: 10px 0;
    border-bottom: $border;
  }
  &__nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__add{
    width: 176px;
    height: 46px;
    background-color: rgba(#b4b4b4,.2);
    border-radius: 5px;
    font-size: 14px;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 0.02em;
    font-family: $font2;
    color: $primary;
    transition: color .3s;
    &--active{
      font-size: 0;
      color: #00355b;
      &:before{
        content: 'добавлено';
        font-size: 14px;
      }
    }
  }
}


@media screen and (max-width:992px){
  .zap-table{
    &__row{
      grid-template-columns: auto 294px;
    }
  }
}


@media screen and (max-width:768px){
  .zap-table{
    &__row{
      grid-template-columns: auto 164px;
    }
    &__title{
      font-size: 14px;
      line-height: 17px;
    }
    &__add{
      width: 46px;
      font-size: 0;
      background-image: url(../img/cart2.png);
      background-position: center;
      background-repeat: no-repeat;
      &:before{
        display: none;
      }
      &--active{
        background-image: url(../img/status.webp);
        background-size: 18px auto;
      }
    }
  }
}


@media screen and (max-width:576px){
  .zap-table{
    &__col{
      &[data-mob-title]{
        font-size: 0;
        &:before{
          content: attr(data-mob-title);
          font-size: 17px;
        }
      }
    }
  }
}
